.spinner {
  transform: -50%, -50%;
  width: 40px;
  height: 40px;
  transition: opacity .35s;
  animation: sk-chase 2.5s linear infinite both;
  position: fixed;
  top: 50%;
  left: 50%;
}

.spinner-visually-hidden {
  opacity: 0;
}

.dot {
  width: 100%;
  height: 100%;
  animation: sk-chase-dot 2s ease-in-out infinite both;
  position: absolute;
  top: 0;
  left: 0;
}

.dot:before {
  content: "";
  width: 25%;
  height: 25%;
  background-color: red;
  border-radius: 100%;
  animation: sk-chase-dot-before 2s ease-in-out infinite both;
  display: block;
}

.dot:nth-child(1) {
  animation-delay: -1.1s;
}

.dot:nth-child(2) {
  animation-delay: -1s;
}

.dot:nth-child(3) {
  animation-delay: -.9s;
}

.dot:nth-child(4) {
  animation-delay: -.8s;
}

.dot:nth-child(5) {
  animation-delay: -.7s;
}

.dot:nth-child(6) {
  animation-delay: -.6s;
}

.dot:nth-child(1):before {
  animation-delay: -1.1s;
}

.dot:nth-child(2):before {
  animation-delay: -1s;
}

.dot:nth-child(3):before {
  animation-delay: -.9s;
}

.dot:nth-child(4):before {
  animation-delay: -.8s;
}

.dot:nth-child(5):before {
  animation-delay: -.7s;
}

.dot:nth-child(6):before {
  animation-delay: -.6s;
}

@keyframes sk-chase {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes sk-chase-dot {
  80%, 100% {
    transform: rotate(360deg);
  }
}

@keyframes sk-chase-dot-before {
  50% {
    transform: scale(.4);
  }

  100%, 0% {
    transform: scale(1);
  }
}

