@charset 'utf-8';
.tui-pagination * {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.tui-ico-ellipsis,
.tui-ico-first,
.tui-ico-last,
.tui-ico-next,
.tui-ico-prev {
  overflow: hidden;
}

.tui-pagination {
  max-width: fit-content;
  height: 40px;
  margin: 60px auto;
  line-height: normal;

  font-size: 0;
}

.tui-pagination .tui-page-btn {
  display: inline-block;
  position: relative;
  width: 40px;
  height: 40px;
  margin-left: 0;
  color: #000000;
  font-size: 12px;
  font-weight: 500;
  line-height: 40px;
  text-decoration: none;
  vertical-align: middle;
  border-radius: 5px;
  text-align: center;
  transition: background-color 255ms linear;
}

.tui-pagination .tui-page-btn:hover {
  background-color: rgba(255, 107, 8, 0.2);
}

.tui-pagination .tui-is-selected,
.tui-pagination strong {
  color: #fff;
  background: #ff6b08;

  cursor: default;
}

.tui-pagination .tui-is-selected:hover {
  background: #ff6b08;
}

.tui-pagination .tui-first-child.tui-is-selected,
.tui-pagination strong.tui-first-child {
  margin-left: 0;
}

