.header {
position: relative;
  color: #ffffff;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  @media screen and (max-width: 480px) {
    background-image: url(/src/images/header/mobile/header_m_1x.jpeg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/src/images/header/mobile/header_m_@2x.jpeg);
    }
  }

  @media screen and (min-width: 480px) {
    background-image: url(/src/images/header/tablet/header_tab_1x.jpeg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/src/images/header/tablet/header_tab_@2x.jpeg);
    }
  }

  @media screen and (min-width: 1200px) {
    background-image: url(/src/images/header/desktop/header_d_1x.jpeg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/src/images/header/desktop/header_d_@2x.jpeg);
    }
  }
}

.library {
  color: #ffffff;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  @media screen and (max-width: 480px) {
    background-image: url(/src/images/header-myLibrary/mobile/mob_1.jpeg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/src/images/header-myLibrary/mobile/mob_1@2x.jpeg);
    }
  }

  @media screen and (min-width: 480px) {
    background-image: url(/src/images/header-myLibrary/tablet/tabl_1.jpeg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/src/images/header-myLibrary/tablet/tabl_1@2x.jpeg);
    }
  }

  @media screen and (min-width: 1200px) {
    background-image: url(/src/images/header-myLibrary/desktop/desc_1.jpeg);

    @media (min-device-pixel-ratio: 2),
      (min-resolution: 192dpi),
      (min-resolution: 2dppx) {
      background-image: url(/src/images/header-myLibrary/desktop/desc_1@2x.jpeg);
    }
  }
}

.container {
  padding-top: 40px;
  padding-bottom: 80px;
  
}

.logo {
  font-family: inherit;
  font-size: 30px;
  font-weight: 500;
  line-height: 1.16;
  letter-spacing: 0em;
  text-align: center;

  display: flex;
  align-items: center;
  gap: 10px;
}

.logo-text {
  @media screen and (max-width: 480px) {
    display: none;
  }
}

.logo-icon {
  fill: transparent;
  stroke: currentColor;
}
.nav-container {
  display: flex;
  align-items: center;
  margin: 0 auto;
  justify-content: space-between;
  margin-bottom: 40px;
  @media screen and (max-width: 480px) {
    margin-bottom: 55px;
  }
}

.nav-list {
  display: flex;
  gap: 40px;

  font-size: 12px;
  font-weight: 500;
  line-height: 1.16;
  text-align: center;
}
.nav-list__item {
  transition-property: color;
  transition-duration: 255ms;
  transition-timing-function: var(--cubic-bezier);
  &:hover {
    color: var(--accent-color);
    cursor: pointer;
  }

  &:focus {
    background-color: var(--accent-color);
    cursor: pointer;
  }
}

.header .current {
  position: relative;

  &::after {
    position: absolute;
    content: '';
    bottom: -5px;
    left: 0px;

    width: 100%;
    height: 3px;

    background-color: #ff001b;
  }
}

.header__search {
  text-align: center;
  width: 100%;
}

.header__form {
  display: flex;
  justify-content: center;
  align-items: center;

  color: #ffffff;
}

.header__input {
  min-width: 240px;

  color: #ffffff;

  background-color: transparent;

  border: none;
  border-bottom: 0.7px solid currentColor;

  outline: unset;

  @media screen and(min-width: 768px) {
    min-width: 336px;
    height: 20px;
  }
  @media screen and(min-width: 1200px) {
    min-width: 395px;
  }
}

input::placeholder {
  line-height: 1.14;
  text-align: left;
  color: inherit;
}

.header__form-btn {
  position: relative;
  left: -18px;
  background-color: transparent;
}

.search-icon {
  stroke: red;
  fill: transparent;
}

.btn-container {
  margin-top: -23px;
  display: flex;
  justify-content: center;
  @media screen and(max-width: 480px) {
    margin-top: 65px;
  }
}

.header__queue-btn {
  margin-left: 16px;
}
//------------------------------------------------------ligth-theme

.change-theme {
  &__btn {
    position: absolute;
    right: 145px;
    bottom: 5px;

    background-color: initial;
    align-content: center;
    border: none;
    text-align: center;
    border-radius: 5px;
    padding: 8px 12px;

   
  }

  &__btn-dark {
    display: none;
  }

  &__icon {
    height: 18px;
    width: 18px;
    fill: #fff;
  }
}

.banner {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 20px;
  font-size: 11px;
  font-weight: 700;
  text-align: center;
  color: white;
  background-image: linear-gradient(to bottom, blue 50%, yellow 50%);
  opacity: 1;
}
